.three-column-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 12px;
  padding-bottom: 100px; }

.three-column-grid img {
  width: 100%;
  -webkit-filter: brightness(1);
          filter: brightness(1);
  border-radius: 3px;
  box-shadow: 0 1px 0px rgba(0, 0, 0, 0.03), 0 1px 1px rgba(0, 0, 0, 0.05); }

.title-image {
