.header {
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-gap: 12px; }
  .header--login a {
    text-decoration: none; }
  .header--login span {
    height: 30px;
    background-color: #1e4f94;
    display: block;
    color: white;
    border-radius: 5px;
    padding: 7px 18px; }
  .header h1 {
    text-indent: -200px;
    height: 55px;
    width: 145px;
    overflow: hidden;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: center; }
  .header .top-nav {
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    grid-gap: 12px;
    justify-items: center;
    align-items: center; }
  .header .top-nav div {
    padding-right: 24px;
    height: 55px;
    justify-items: center;
    align-items: center;
    display: grid;
    position: relative;
    font-weight: 500; }
  .header .top-nav div::after {
    width: 6px;
    height: 10px;
    content: "";
    right: 0;
    position: absolute;
    -webkit-transform: translate(-7px, 0px) rotate(90deg);
            transform: translate(-7px, 0px) rotate(90deg);
    background-image: url(/marketinigWepApp/static/media/blue-chevron.7b21f96e.svg);
    background-repeat: no-repeat;
    background-position: center; }
  .header .dropdown-button {
    position: relative;
    cursor: pointer; }
  .header .dropdown {
    font-weight: 400;
    position: absolute;
    top: 49px;
    right: 0px;
    z-index: 50;
    display: none;
    background-color: white;
