    border-radius: 5px;
    padding: 6px 0; }
  .header .dropdown a {
    display: block;
    width: auto;
    text-decoration: none;
    padding: 6px 30px 6px 12px;
    color: #303742;
    cursor: pointer;
    position: relative;
    white-space: nowrap; }
  .header .dropdown a:hover {
    display: block;
    width: 100%;
    text-decoration: none;
    padding: 6px 30px 6px 12px;
    background-color: #ebeced; }
  .header .dropdown a:hover::before {
    content: "";
    position: absolute;
    width: 3px;
    height: 28px;
    background-color: #3081e3;
    left: 0;
    top: 0; }

@media (prefers-color-scheme: dark) {
