.carousel {
  height: 500px;
  display: block;
  margin: 33px 0 45px 0;
  border-radius: 5px;
  position: relative;
  box-shadow: $level-1;
  border: 1px solid #cecece;
  filter: brightness(1);
  overflow: hidden;

  .left-button {
    display: none;
    position: absolute;
    left: 0;
    top: 190px;
    width: 65px;
    height: 75px;
    background-color: $isabelline;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    background-image: url(../../images/carousel/left-light.svg);
    background-repeat: no-repeat;
    background-position: center;
    left: -1px;
    border-top: 1px solid #cecece;
    border-right: 1px solid #cecece;
    border-bottom: 1px solid #cecece;
  }

  .right-button {
    display: none;
    position: absolute;
    right: 0;
    top: 190px;
    width: 65px;
    height: 75px;
    background-color: $isabelline;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    background-image: url(../../images/carousel/right-light.svg);
    background-repeat: no-repeat;
    background-position: center;
    right: -1px;
    border-top: 1px solid #cecece;
    border-left: 1px solid #cecece;
    border-bottom: 1px solid #cecece;
  }
}

@media (prefers-color-scheme: dark) {
  .carousel {
    border: 1px solid #000;
    filter: brightness(0.7);
    .left-button {
      background-color: $dark-lava;
      background-image: url(../../images/carousel/left-dark.svg);
      border-top: 1px solid #000;
      border-right: 1px solid #000;
      border-bottom: 1px solid #000;
    }
    .right-button {
      background-color: $dark-lava;
      background-image: url(../../images/carousel/right-dark.svg);
      border-top: 1px solid #000;
      border-left: 1px solid #000;
      border-bottom: 1px solid #000;
    }
  }
}

@media (prefers-color-scheme: light) {
  .carousel {
    border: 1px solid #cecece;
    filter: brightness(1);
    .left-button {
      background-color: $isabelline;
      background-image: url(../../images/carousel/left-light.svg);
      border-top: 1px solid #cecece;
      border-right: 1px solid #cecece;
      border-bottom: 1px solid #cecece;
    }
    .right-button {
      background-color: $isabelline;
      background-image: url(../../images/carousel/right-light.svg);
      border-top: 1px solid #cecece;
      border-left: 1px solid #cecece;
      border-bottom: 1px solid #cecece;
    }
  }
}
